.row{
    margin-left: 20px;
}

.movies{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;

}

.movies::-webkit-scrollbar{
    display: none;
}

.movie{
    width: 100%;
    max-height: 150px;
    margin-right: 10px;
}
.movie:hover{
    transform: scale(1.05);
}
.largeRow{
    max-height: 350px;
}